<template>
  <div class="container">
    <div class="container-content">
      <div class="summary">
        <div v-for="item in infoList" :key="item.prop" class="summary-item">
          <!-- <i class="el-icon-user summary-item-icon" /> -->
          <div class="summary-item-content">
            <div class="summary-item-title">{{ item.label }}</div>
            <div class="summary-item-used">
              {{ itemInfoFormatter(item) }}
            </div>
          </div>
        </div>
      </div>
      <div class="container-bottom">
        <div class="container-form">
          <el-form ref="qryParamRef" :model="qryParam" size="small">
            <el-row :gutter="20">
              <el-col :span="5">
                <el-form-item :label="$t('traffic.accessNum')" prop="accNum">
                  <el-input
                    v-model="qryParam.accNum"
                    :placeholder="inputPh('traffic.accessNum')"
                  ></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="19">
                <el-form-item>
                  <div class="search-button-group">
                    <el-button
                      type="primary"
                      icon="el-icon-search"
                      @click="loadData(false)"
                      >{{ $t('common.query') }}</el-button
                    >
                    <el-button type="info" icon="el-icon-refresh" @click="reset">{{
                      $t('common.reset')
                    }}</el-button>
                  </div>
                </el-form-item>
              </el-col>
            </el-row>
            <div class="tool-button-group">
              <BatchExport
                query-id="xxxx"
                :params="simListExportParam"
                :disabled="tableData.length === 0"
              ></BatchExport>
            </div>
          </el-form>
          <el-table ref="table" v-loading="loading" :data="tableData" :border="true">
            <el-table-column
              prop="iccid"
              :label="$t('traffic.memberNumber')"
              show-overflow-tooltip
            ></el-table-column>
            <el-table-column
              prop="statusCd"
              :label="$t('traffic.statusCdName')"
              show-overflow-tooltip
            ></el-table-column>
            <el-table-column
              prop="statusCdDesc"
              :label="$t('traffic.memberSize')"
              show-overflow-tooltip
            ></el-table-column>
            <el-table-column
              prop="statusDate"
              :label="$t('traffic.usedSize')"
              show-overflow-tooltip
            ></el-table-column>
          </el-table>
          <el-pagination
            v-show="tableData.length > 0"
            background
            :current-page="page.pageIndex"
            align="right"
            :page-sizes="[10, 20, 50, 100]"
            :page-size="page.pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="page.total"
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
          >
          </el-pagination>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import langMixin from '@/mixins/langMixin'
export default {
  mixins: [langMixin],
  data() {
    return {
      importTaskId: '',
      tableData: [],
      loading: false,
      page: {
        pageIndex: 1,
        pageSize: 10,
        total: 0,
      },
      detailInfo: {},
      gryInitsimStatusRes: [],
      activeTab: '1',
      qryParam: {
        accNum: '',
        batchListStatus: '',
      },
    }
  },
  computed: {
    simListExportParam() {
      return { ...this.qryParam, grpNumber: this.grpNumber }
    },
    infoList() {
      return [
        {
          label: this.$t('traffic.groupId'),
          prop: 'importTaskId',
        },
        {
          label: this.$t('traffic.billCycle'),
          prop: 'statusCdDesc',
        },
        {
          label: this.$t('traffic.usageAvaMonth'),
          prop: 'supplierDesc',
        },
        {
          label: this.$t('traffic.usageUsedMonth'),
          prop: 'ispDesc',
        },
        {
          label: this.$t('traffic.usageRemainMonth'),
          prop: 'custName',
        },
      ]
    },
  },
  created() {
    this.importTaskId = this.$route.query.importTaskId
  },
  mounted() {
    // this.qryMktImportTaskDetail()
    // this.loadData(false)
    // this.gryInitsimStatus()
  },
  methods: {
    async gryInitsimStatus() {
      this.gryInitsimStatusRes = await this.$api.mktImportTask.qryInitSimStatus()
    },
    qryMktImportTaskDetail() {
      const param = { importTaskId: this.importTaskId }
      this.$api.mktImportTask
        .qryMktImportTaskDetail(param)
        .then((res) => {
          this.detailInfo = res
        })
        .finally(() => {
          this.loading = false
        })
    },
    loadData(pageChangeFlag) {
      if (!pageChangeFlag) {
        this.page.pageIndex = 1
        this.page.pageSize = 10
      }
      const param = {
        batchTaskId: this.importTaskId,
        ...this.qryParam,
        ...this.page,
      }
      this.loading = true
      this.$api.mktImportTask
        .qryMktBatchTaskList(param)
        .then((res) => {
          this.tableData = res.list || []
          this.page.total = res.total
        })
        .finally(() => {
          this.loading = false
        })
    },
    itemInfoFormatter(obj) {
      const des = obj.formatter
        ? obj.formatter(this.detailInfo[obj.prop] ?? '-')
        : this.detailInfo[obj.prop] ?? '-'
      return des
    },
    handleSizeChange(val) {
      this.page.pageSize = val
      this.page.pageIndex = 1
      this.loadData(true)
    },
    handleCurrentChange(val) {
      this.page.pageIndex = val
      this.loadData(true)
    },
    exportClick() {},

    reset() {
      this.$refs.qryParamRef.resetFields()
      this.loadData(false)
    },
  },
}
</script>
